@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: cadetblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

.header {
  text-align: center;
}

label {
  display: inline-block;
  width: 100px;
  -webkit-flex: 0 0 100px;
          flex: 0 0 100px;
}

input, select {
  width: 300px;
  padding: 5px;
  border: 1px solid grey;
  border-radius: 5px;
}

.form-group {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 5px;
}

.add-form-card{box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.3s;width:500px;border-radius:5px;background-color:white;padding:10px}@media screen and (max-width: 600px){.add-form-card{width:300px}}.container{max-width:950px;margin:0 auto}.input-group{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;width:100%}.input-group input,.input-group span,.input-group select{width:100%}

.personal-item {
  margin: 5px;
  width: 300px;
}

@media screen and (max-width: 600px) {
  .personal-item {
    width: 200px;
  }
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 100%;
  border-radius: 5px;
  background-color: white;
  padding: 10px 16px;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.action-btns {
  text-align: end;
}

button {
  border: none;
  border-radius: 5px;
  padding: 5px 10px;  
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}

.edit-btn {
  background-color: white; 
  color: black; 
  border: 2px solid #008CBA;
}

.edit-btn:hover {
  background-color: #008CBA;
  color: white;
}

.delete-btn {
  background-color: white; 
  color: black; 
  border: 2px solid #f44336;
}

.delete-btn:hover {
  background-color: #f44336;
  color: white;
}
.add-btn {
  text-align: end;
  margin-right: 10px;
}

.card-list {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
